@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Mohave:wght@300&display=swap");

body {
  margin: 0;
  font-family: Lato, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --main-background: #152238;
  --neon-purple: #b026ff;
  --neon-purple-secondary: rgba(176, 38, 255, 0.2);
}
